<script>
  import {mapGetters} from 'vuex'
  import Loading from '../Utilities/LoadingIndicator'
  export default {
    name: 'UserOptions',
    components: { Loading },
    data () {
      return {
        isLoading: false
      }
    },
    computed: {
      ...mapGetters(['authUser'])
    },
    methods: {
      refreshUser () {
        this.isLoading = true
        this.$store.dispatch('init_auth_user', {request: 'refresh'}).then(() => {
          this.isLoading = false
        })
      },
      logout () {
          this.$store.dispatch('logout')
          this.$router.push({name: 'home'})
      }
    }
  }
</script>

